.invernadero {
  width: 100%;
  height: 80rem;
  background: url(../../../build/img/campos.jpg);
  background-size: 100vw auto;
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
}
.invernadero .zona {
  position: sticky;
  top: 15rem;
  left: 65rem;
  width: 27rem;
  height: 18rem;
  background-color: transparent;
}

.contenedorInfoCampo {
  display: flex;
  align-items: center;
  justify-content: center;
}
.contenedorInfoCampo .nombreCampo {
  font-family: "Fraunces", serif;
  font-size: 3rem;
  color: white;
}

.infoCampo1 {
  margin-left: 2rem;
  width: 55rem;
  height: 40rem;
  background-color: rgba(0, 0, 0, 0.66);
  border-radius: 1rem;
}

.parametrosCampo {
  width: 49rem;
  height: 68%;
  margin: auto;
  background-color: aqua;
}

.contenedorDescripcion {
  width: 49rem;
  height: 10rem;
  display: grid;
  grid-template-columns: repeat(2, 0.3fr);
  gap: 2rem;
  margin: auto;
  background-color: rebeccapurple;
}

.contenedorVista {
  width: 100%;
  height: 10rem;
  background-color: red;
}

.contenedorDimensiones {
  width: 100%;
  height: 10rem;
  background-color: blue;
}